.legend-item-container {
  padding: 15px;
}

.legend-title {
  font-family: OpenSans-Bold;
  font-size: 12px;
  /*line-height: 1.2;*/
  line-height: 2.0;
  color: #ffffff; 
}

.legend-item {
  min-height: 70px; 
  padding: 20px 15px 15px 15px;
}
.legend-item-hide {
  display: none;
}

.vcenter {
  display: inline-block !important;
  vertical-align: middle;
  /*float: none;*/
}
.swatch-wrapper {
  display: flex;
  padding-left: 10px;
  padding-top: 5px;
}
.legend-item-swatch {
  width: 14px;
  height: 9px;
}
.legend-item-padding {
  padding-left: 5px;
  padding-right: 5px;
}

.legend-item-swatch .fs-image {
  width: 11px;
  height: 13px;
}

.swatch-kml {
  margin-top: 8px;
}

.legend-options {
  color: white;
  font-size: 20px;
  cursor: pointer;
}

.legend-options span {
  margin-right: 8px;
}

.legend-view >div:not(.legend-item-hide) {
  border-bottom: 1px solid #263e57;
}

.arcgis-link, .arcgis-link:hover,.arcgis-link:visited,.arcgis-link:active {
  text-decoration: none;
  color: white;
}
.rotate-270 {
  -webkit-transform: rotate(270deg);
  -moz-transform: rotate(270deg);
  -o-transform: rotate(270deg);
  -ms-transform: rotate(270deg);
  transform: rotate(270deg);
}